/* Slider.css */
.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 30px;
}

.gallery-image {
    width: 100%;
    max-width: 200px; /* Максимальная ширина для изображений */
    height: auto;
    margin: 10px;
    cursor: pointer;
    transition: transform 0.2s, filter 0.2s, box-shadow 0.2s; /* Плавный эффект при наведении */
}

.gallery-image:hover {
    transform: scale(1.15); /* Увеличение изображения при наведении */
    filter: brightness(1.3); /* Увеличение яркости при наведении */
    box-shadow: 0 4px 15px rgba(139, 0, 0, 1); /* Добавление тени */
}

.modal {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1000;
}

.modal-content {
    max-width: 90%;
    max-height: 90%;
}

.close, .prev, .next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: white;
    font-size: 30px;
    cursor: pointer;
    @media (max-width: 426px) {
        top: 88%;
    }
    
}

.close {
    font-size: 80px;
    top: 36px;
    right: 20px;
}

.prev {
    left: 0px;
}

.next {
    right: 0px;
}